/* ===== MODERN HOME PAGE DESIGN ===== */

/* ---- ROOT CONTAINER ---- */
.home-root {
  width: 100%;
  min-height: 100vh;
  background: #ffffff;
  position: relative;
  overflow-x: hidden;
}

/* ---- ERROR BANNER ---- */
.error-banner {
  position: fixed;
  top: 80px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(135deg, #ff6b6b 0%, #ee5a52 100%);
  color: white;
  padding: 16px 24px;
  border-radius: 50px;
  z-index: 1000;
  font-weight: 600;
  animation: slideDownError 0.5s ease-out;
}

@keyframes slideDownError {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

/* ---- NAVIGATION SPACING ---- */
.public-container {
  padding-top: 120px;
  display: flex;
  flex-direction: column;
  gap: 80px;
  position: relative;
  z-index: 10;
}

/* ---- HERO SECTION: MODERN SPLIT DESIGN ---- */
.hero-section {
  padding: 60px 40px;
  max-width: 1400px;
  margin: 0 auto;
}

.hero-content {
  display: flex;
  align-items: stretch;
  gap: 80px;
  min-height: 500px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* BANNER SIDE - LEFT */
.banner-container {
  flex: 3;
  position: relative;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
  overflow: hidden;
}

.website-banner {
  width: 100%;
  height: auto;
  max-height: 450px;
  border-radius: 16px;
  position: relative;
  z-index: 2;
  transition: transform 0.3s ease;
}

.website-banner:hover {
  transform: scale(1.02);
}

/* LOGIN SIDE - RIGHT */
.login-container {
  flex: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 60px 40px;
  background: #ffffff;
}

.login-container > * {
  width: 100%;
  max-width: 380px;
  background: white;
  padding: 40px 30px;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  transition: all 0.3s ease;
}

.login-container > *:hover {
  transform: translateY(-8px);
}

/* ---- ABOUT & EMPLOYEES SECTIONS ---- */
.section-block {
  padding: 80px 40px;
  max-width: 1200px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(15px);
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  margin-bottom: 40px;
  transition: transform 0.3s ease;
}

.section-block:hover {
  transform: translateY(-5px);
}

/* ---- RESPONSIVE DESIGN ---- */

/* Tablet */
@media (max-width: 1024px) {
  .hero-content {
    flex-direction: column;
    gap: 0;
    min-height: auto;
  }

  .banner-container {
    flex: none;
    min-height: 400px;
  }

  .login-container {
    flex: none;
    padding: 40px 20px;
  }

  .website-banner {
    max-width: 80%;
    max-height: 350px;
  }

  .section-block {
    padding: 60px 30px;
    margin: 0 20px 40px;
  }
}

/* Mobile */
@media (max-width: 768px) {
  .public-container {
    padding-top: 100px;
    gap: 60px;
  }

  .hero-section {
    padding: 40px 20px;
  }

  .hero-content {
    border-radius: 16px;
  }

  .banner-container {
    padding: 30px 20px;
    min-height: 300px;
  }

  .login-container {
    padding: 30px 20px;
  }

  .login-container > * {
    padding: 40px 30px;
  }

  .website-banner {
    max-width: 100%;
    max-height: 280px;
  }

  .section-block {
    padding: 40px 20px;
    margin: 0 10px 30px;
    border-radius: 16px;
  }

  .error-banner {
    top: 70px;
    padding: 12px 20px;
    font-size: 14px;
  }
}

/* Small Mobile */
@media (max-width: 480px) {
  .hero-section {
    padding: 20px 15px;
  }

  .banner-container {
    padding: 20px 15px;
    min-height: 250px;
  }

  .login-container {
    padding: 20px 15px;
  }

  .login-container > * {
    padding: 30px 20px;
  }

  .website-banner {
    max-height: 220px;
  }

  .section-block {
    padding: 30px 15px;
    margin: 0 5px 20px;
  }
}

/* ---- ANIMATIONS & EFFECTS ---- */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-section,
.section-block {
  animation: fadeInUp 0.8s ease-out;
}

.hero-section:nth-child(1) { animation-delay: 0.2s; }
.section-block:nth-child(2) { animation-delay: 0.4s; }
.section-block:nth-child(3) { animation-delay: 0.6s; }

/* ---- LOADING SCREEN ---- */
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.loading-screen p {
  color: white;
  font-size: 18px;
  font-weight: 600;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
}
